        height: auto; /* Maintain aspect ratio */
    }
    
    .nav-menu {
        display: none;
        flex-direction: column;
        width: 100%;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 100%;
        left: 0;
        padding: 20px;
        border-radius: 0 0 10px 10px;
        box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
        z-index: 999;
        color: white;
    }
    
    .nav-menu-visible {
        display: flex !important;
    }

    .nav-logo p {
        font-size: 22px;
    }
    
    .nav-menu li {
        margin-bottom: 15px;
    }
    
