.productdisplay {
    display: flex;
    margin: 0px 170px;
}
.productdisplay-left {
    display: flex;
    gap: 17px;

}
.productdisplay-img-list {
    display: flex;
    flex-direction: column;
    gap: 16px;

}
.productdisplay-img-list img {
    height: 163px;
}
.productdisplay-main-img {
    width: 586px;
    height: 700px;
}
.productdisplay-right {
    display: flex;
    flex-direction: column;
    margin: 0px 70px;
}

.productdisplay-right h1 {
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 700;

}
.productdisplay-right-stars {
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;

}
.productdisplay-right-prices {
    display: flex;
    margin: 40px 0px;
    gap: 30px;
    font-size: 24px;
    font-weight: 700;
}
.productdisplay-right-price-old {
    color: #818181;
    text-decoration: line-through;
}
.productdisplay-right-price-new {
    color: #c93fef;
    
}

.productdisplay-right-size h1 {
    margin-top: 55px;
    color: #926d5c;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-sizes {
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.productdisplay-right-sizes div {
    padding: 18px 24px;
    background:#9CA986;
    transition: transform 0.2s, box-shadow 0.2s, background 0.3s;
    border: 1px solid #ebebeb;
    border-radius: 4px;
    cursor: pointer;
}

.productdisplay-right-sizes div:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);

}

.productdisplay-right-sizes div:active {
    background: linear-gradient(90deg, #d1b386 0%, #d1b386 40%);

}

.productdisplay-right button {
    padding: 20px 40px;
    width: 200px;
    font-size: 16px;
    font-weight: 600;
    border: none;
    color: white;
    background: #d1b386;
    margin-bottom: 40px;
    outline: none;
    cursor: pointer;
    transition: transform 0.2s, box-shadow 0.2s, background 0.3s;
}

.productdisplay-right button:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.productdisplay-right button:active {
    background: linear-gradient(90deg, #ce79e6 0%, #d178ed 40%);

}

.productdisplay-right-category {
    margin-top: 10px;
}

.productdisplay-right-category span {
    font-weight: 600;
}
@media(max-width:1280px) {
    .productdisplay {
        margin: 0px 60px;
    }
    .productdisplay-left {
        gap: 10px;
    }
    .productdisplay-img-list {
        gap: 10px;
    }
    .productdisplay-img-list img {
        height: 120px
    }
    .productdisplay-main-img {
        width: auto;
        height: 510px;
    }
    .productdisplay-right {
        margin: 0px 30px;
    }
    .productdisplay-right h1 {
        font-size: 22px;
    }
    .productdisplay-right-stars {
        font-size: 13px;
        gap: 3px;
        
    }
    .productdisplay-right-prices {
        margin: 10px 0px;
        font-size: 18px;
    }
    .productdisplay-right-description {
        font-size: 13px;
    }
    .productdisplay-right-size h1 {
        margin-top: 20px;
        font-size: 20px
    }
    .productdisplay-right-sizes div {
        padding: 14px 20px;
    }
    .productdisplay-right button {
        padding: 15px 0px;
        width: 150px;
        margin-bottom: 20px;
    }
    .productdisplay-right-category {
        margin-top: 5px;
        font-size: 14px;
    }
}
@media (max-width: 1024px) {
    .productdisplay {
        margin: 0px 30px;
    }
    .productdisplay-left {
        gap: 5px;
    }
    .productdisplay-img-list img {
        height: 80px
    }
    .productdisplay-main-img {
        height: 350px;
    }
    .productdisplay-right h1 {
        font-size: 18px;
    }
    .productdisplay-right-stars img {
        width: 15px;
    }
    .productdisplay-right-description {
        font-size: 12px;
    }
    .productdisplay-right-sizes {
        margin-top: 10px 0;
    }
    .productdisplay-right-sizes div {
        padding: 10px 16px;
        font-size: 12px;
    }
    .productdisplay-right button {
        padding: 10px 0px;
        width: 120px;
        margin-bottom: 10px;
        font-size: 14px;
    }
    .productdisplay-right-category {
        font-size: 12px;
    }
}
@media (max-width: 800px) {
    .productdisplay {
        margin: 0px 10px;
    }
    .productdisplay-left {
        gap: 0px;
        flex-direction: column-reverse;
        align-items: center;
    }
    .productdisplay-img-list {
        flex-direction: row;
        gap: 6px;
    }
    .productdisplay-img-list img {
        height: 70px
    }
    .productdisplay-main-img {
        height: 300px;
    }
    .productdisplay-right h1 {
        font-size: 14px;
    }
    .productdisplay-right-stars img {
        width: 13px;
    }
    .productdisplay-right-description {
        font-size: 10px;
    }
    .productdisplay-right-size h1 {
        margin-top: 10px 0px;
    }
    .productdisplay-right-sizes {
        margin-top: 10px;
        gap: 5px;
    }
    .productdisplay-right-sizes div {
        padding: 4px 11px;
    
    }
    .productdisplay-right button {
        width: 100px;
        font-size: 12px;
    }
    .productdisplay-right-category {
        font-size: 10px;
    }
}
@media (max-width: 500px) {
    .productdisplay {
        flex-direction: column;

    }
    .productdisplay-left {
        gap: 10px;
        flex-direction: row;
        margin: auto;
    }
    .productdisplay-img-list {
        flex-direction: column;
        gap: 8px;
    }
    .productdisplay-img-list img {
        height: 75px;
    }
    .productdisplay-main-img {
        height: 330px;
    }
    .productdisplay-right {
        margin: 5px;
    }
    .productdisplay-right h1{
        margin-top: 15px;
        font-size: 20px;
        font-weight: 500;
    }
    .productdisplay-right-stars img {
        width: 15px;
    }
    .productdisplay-right-sizes {
        gap: 10px;
        margin: 20px 0px;
    }
    .productdisplay-right-sizes div {
        padding: 10px 16px;
        font-size: 14px;
    }
    .productdisplay-right button {
        padding: 12px 0px;
        width: 130px;
        font-size: 15px;
    }
    .productdisplay-right-category {
        font-size: 16px;
    }
}



